export default function App() {
  return (
    <div className="App">
      <grid>
        <BrowserRouter basemname={`/${process.env.PUBLIC_URL}`}>
          <Header></Header>

          <Route exact={true} path="/" component={Work} />
        </BrowserRouter>

        <Footer></Footer>
      </grid>
    </div>
  );
}
